.text-center {
  margin-left: auto;
  margin-right: auto;
}

.filtres {
  background-color: #474747;
  color: white;
  padding: 20px 10px;
  font-size: 22px;
  display: flex;
  justify-content: space-between;
}

.input-range {
  display: inline-block;
  margin-left: 15px;
  width: 200px !important;
}

.leaflet-container {
  height: 400px;
  width: 100%;
}

.vehiclePopup {
  display: flex;
}

.vehiclePopup .textColumn {
  font-size: 14px;
  white-space: nowrap;
  font-weight: 600;
  color: #4f4f4f;
}

.filterLabel {
  display: inline;
  margin-left: 10px;
  width: 400px;
}

.vehiclePopup .photoColumn img {
  height: 50px;
  width: 50px;
  margin-left: 30px;
}

.myProgress {
  width: 50px;
  background-color: grey;
  /* display: inline-block; */
  margin-left: 30px;
  margin-top: 8px;
}

.myBar {
  width: 1%;
  height: 10px;
  background-color: green;
}

.rangeText {
  display: inline-block;
}

.statusWrapper {
  background-color: #28A745;
  color: white;
  padding-top: 4px;
  padding-bottom: 4px;
  text-align: center;
  border-radius: 15px;
  margin-top: 8px;
}

/* CUSTOM CHECKBOX */

.chechbox-container {
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin-bottom: 0;
}

.chechbox-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 50%;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
  transform: translateY(-50%);
}

.chechbox-container:hover input~.checkmark {
  background-color: #ccc;
}

.chechbox-container input:checked~.checkmark {
  background-color: #4CAF50;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.chechbox-container input:checked~.checkmark:after {
  display: block;
}

.chechbox-container .checkmark:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}


@media (max-width: 768px) {
  .input-range {
    width: 100px !important;
  }

  .filterLabel {
    width: 300px;
  }

}

@media (max-width: 500px) {
  .filtres {
    flex-direction: column;
  }

  .filterLabel {
    margin-left: 0;
  }
}

@media (max-width: 450px) {
  .filtres {
    font-size: 17px;
  }

  .chechbox-container {
    padding-left: 23px;
  }

  .checkmark {
    height: 17px;
    width: 17px;
  }

  .chechbox-container .checkmark:after {
    left: 7px;
    top: 3px;
    width: 5px;
    height: 10px;
  }
}

@media (max-width: 350px) {
  .filterLabel {
    margin-right: 16px;
    width: auto;
  }
}